/**
 * Admin Offer Refund Metabox Styles
 *
 * @since 10.4.0
 */

.cfw-offer-refund-metabox {
	.cfw-offer-refund-table {
		width: 100%;
		border-collapse: collapse;
		margin-bottom: 15px;

		thead {
			th {
				text-align: left;
				padding: 8px;
				border-bottom: 2px solid #ddd;
				font-weight: 600;
				font-size: 12px;
				color: #555;
			}
		}

		tbody {
			tr {
				border-bottom: 1px solid #f0f0f0;

				&:last-child {
					border-bottom: none;
				}

				td {
					padding: 10px 8px;
					vertical-align: middle;
					font-size: 13px;

					&.cfw-offer-thumb {
						width: 60px;
						text-align: center;

						img {
							max-width: 50px;
							height: auto;
							border-radius: 3px;
							border: 1px solid #ddd;
						}
					}

					&.cfw-offer-name {
						strong {
							display: block;
							margin-bottom: 4px;
							font-size: 13px;
						}

						.cfw-offer-variation {
							font-size: 11px;
							color: #666;
							margin-top: 4px;

							div {
								margin-bottom: 2px;
							}
						}
					}

					&.cfw-offer-qty {
						text-align: center;
						width: 50px;
					}

					&.cfw-offer-total {
						text-align: right;
						width: 80px;
						font-weight: 600;
					}

					&.cfw-offer-action {
						text-align: center;
						width: 90px;
					}

					&.cfw-no-offers {
						text-align: center;
						padding: 20px;
						color: #666;
						font-style: italic;
					}
				}
			}
		}
	}

	.cfw-refund-offer-button {
		padding: 5px 12px;
		font-size: 12px;
		height: auto;
		line-height: 1.4;

		&.cfw-refunding {
			opacity: 0.6;
			cursor: wait;
		}

		&:disabled {
			opacity: 0.5;
		}
	}

	.cfw-offer-refunded {
		display: inline-block;
		padding: 5px 12px;
		background: #f0f0f0;
		color: #666;
		border-radius: 3px;
		font-size: 12px;
		font-weight: 500;
	}

	.cfw-offer-refund-note {
		padding: 12px;
		background: #f9f9f9;
		border: 1px solid #e5e5e5;
		border-radius: 3px;

		p {
			margin: 0;
			font-size: 12px;
			line-height: 1.5;
			color: #666;

			strong {
				color: #333;
			}
		}
	}
}
